                $('.filter-by').on('change', function () {
                    var val = $(this).val()
                    if (val == "Violations") {
                        max = 100
                        min = 1
                        changingSeriesData()
                        redColorScheme()
                        unitsTooltips()

                        Mapchart.legend.title.attr({
                            text: "Expired Certificates By Sub County"
                        })

                        Mapchart.series[0].update({
                            name: "Expired Certificates By Sub County"
                        })
                    }

                    if (val == "Revenue") {
                        max = 10000000
                        min = 1
                        changingSeriesData()
                        changingLegendColor()
                        cashTooltips()

                        Mapchart.legend.title.attr({
                            text: "Revenue Collection By Sub-Counties"
                        })

                        Mapchart.series[0].update({
                            name: "Revenue Collection By Sub-Counties"
                        })
                    }

                })

//Cash legend
                function unitsTooltips() {
                    Mapchart.series[0].update({
                        tooltip: {
                            pointFormat: '<span style="font-weight:500;">{point.name} Sub County: </span>{point.value}',
                            shared: true
                        },
                    })
                }

//cash legend
                function cashTooltips() {
                    Mapchart.series[0].update({
                        tooltip: {
                            pointFormat: '<span style="font-weight:500;">{point.name} Sub County: </span>KES {point.value}',
                            shared: true
                        },
                    })
                }

//creationg random numbers
                function getRandomNumber(min, max) {
                    return Math.floor(Math.random() * (max - min + 1)) + min
                }


                function changingLegendColor() {
                    //alert("we changing axis color")
                    Mapchart.colorAxis[0].update({
                        min: 1,
                        stops: [
                            [0, '#ebedf0'],
                            [0.2, '#c6e6d0'],
                            [0.4, '#7dc794'],
                            [0.6, '#46af68'],
                            [0.7, '#358e52'],
                            [1, '#215d34']
                        ],
                    })
                }

                function redColorScheme() {
                    Mapchart.colorAxis[0].update({
                        min: 1,
                        stops: [
                            [0, '#ebedf0'],
                            [0.2, '#ffcacd'],
                            [0.4, '#f16964'],
                            [0.6, '#ff2a15'],
                            [0.7, '#df0013'],
                            [1, '#c50000']
                        ]
                    })
                }


                function percentageTooltip() {
                    Mapchart.series[0].update({
                        tooltip: {
                            pointFormat: '<span style="font-weight:500;">{point.name} Sub County: </span> {point.value} %',
                            shared: true
                        },
