            getUsers() {
                if (!this.refresh) return
                const data = new FormData();
                data.append("function", "getUsers");
                execute(data, authUrl)
                    .then((res) => {
                        if (res.data.success) {
                            this.activeAgent =0
                            this.inactiveAgent =0
                            this.dormantAgent =0
                            this.users.splice(0)
                            this.users = res.data.data.users
                            this.initMap()

                        } else {
                            this.message = res.data.message
                        }
                    })
                    .catch((e) => {
                        //alert(e.message);
                        this.message = e.message
                    });
            },
            dashboardSummary() {

                const data = new FormData();
                data.append("function", "dashboardSummary");
                data.append("month", this.selectedMonth);
                data.append("year", this.selectedYear);
                execute(data, biller)
                    .then((res) => {


                        if (res.data.success) {
                            //clear

                            //set
                            this.monthlyBillReceipted = res.data.data.monthlyBillReceipted
                            this.dashboard = res.data.data.dashboard
                            this.dates = res.data.data.dates
                            this.years = res.data.data.years


                            this.departmentRevenue.splice(0, this.departmentRevenue.length);
                            this.zonesRevenue.splice(0, this.zonesRevenue.length);
                            this.subCountiesRevenue.splice(0, this.subCountiesRevenue.length);
                            this.wardsRevenue.splice(0, this.wardsRevenue.length);
                            this.categories.splice(0, this.categories.length);
                            this.billData.splice(0, this.billData.length);
                            this.receiptData.splice(0, this.receiptData.length);

                            this.departmentRevenue = res.data.data.departmentRevenue
                            this.subCountiesRevenue = res.data.data.subCountiesRevenue
                            this.wardsRevenue = res.data.data.wardsRevenue
                            this.zonesRevenue = res.data.data.zonesRevenue


                            for (let item of this.monthlyBillReceipted) {
                                this.categories.push(item.month);
                                this.billData.push(item.totalBillCreated)
                                this.receiptData.push(item.totalReceiptAmount)
                            }


                            if (this.refreshMaps) {
                                this.chat()
                                this.revenueSources()
                                this.dataMapping()
                                this.departmentRev()
                                this.refreshMaps = false
                            }


                            //compute totals department
                            this.totalAmountDepartment = this.departmentRevenue.reduce((acc, cur) => {
                                let totalAmount = Number(cur.totalAmount);
                                return acc + totalAmount;
                            }, 0);
                            this.totalTodayDepartment = this.departmentRevenue.reduce((acc, cur) => {
                                let todayAmount = Number(cur.todayAmount);
                                return acc + todayAmount;
                            }, 0);

                            //compute totals zones
                            this.totalAmountZone = this.zonesRevenue.reduce((acc, cur) => {
                                let totalAmount = Number(cur.totalAmount);
                                return acc + totalAmount;
                            }, 0);
                            this.totalTodayZone = this.zonesRevenue.reduce((acc, cur) => {
                                let todayAmount = Number(cur.todayAmount);
                                return acc + todayAmount;
                            }, 0);


                            //compute totals wards
                            this.totalAmountWard = this.wardsRevenue.reduce((acc, cur) => {
                                let totalAmount = Number(cur.totalAmount);
                                return acc + totalAmount;
                            }, 0);
                            this.totalTodayWard = this.wardsRevenue.reduce((acc, cur) => {
                                let todayAmount = Number(cur.todayAmount);
                                return acc + todayAmount;
                            }, 0);

                            //compute totals sub county
                            this.totalAmountSubCounty = this.subCountiesRevenue.reduce((acc, cur) => {
                                let amountTotal = Number(cur.amountTotal);
                                console.log("####\n")
                                if (!isNaN(amountTotal)) {
                                    return acc + amountTotal;
                                } else {
                                    return acc;
                                }
                            }, 0);
                            this.totalTodaySubCounty = this.subCountiesRevenue.reduce((acc, cur) => {
                                let amountToday = Number(cur.amountToday);
                                if (!isNaN(amountToday)) {
                                    return acc + amountToday;
                                } else {
                                    return acc;
                                }
                            }, 0);


                        } else {
                            this.message = res.data.message
                        }
                    })
                    .catch((e) => {
                        //alert(e.message);
                        this.message = e.message
                    });
            },
            humanDateTimeAgo(date) {

                return moment(date).fromNow();

            },
            formattedTime(time) {
                return moment(time).format('hh:mm A')
            },
            getStatus(time) {
                const now = Date.now();
                const then = Date.parse(time);
                const elapsed = now - then;
                if (elapsed < 7200000) {
                    return "Active";
                } else if (elapsed < 259200000) {
                    return "Inactive";
                } else {
                    return "Dormant";
                }
            },
            initMap() {

                const latitude = -0.5411116516680171;
                const longitude = 34.46165980130826;

                const success = (position) => {
                    const latitude = position.coords.latitude;
                    const longitude = position.coords.longitude;
                    // Do something with the position
                };

                const error = (err) => {
                    console.log(error)
                };

                // This will open permission popup
                navigator.geolocation.getCurrentPosition(success, error);

                let map;
                map = new google.maps.Map(document.getElementById("map"), {
                    zoom: 13,
                    center: {lat: latitude, lng: longitude}, // you can use any default center
                });


                this.users.forEach((item) => {

                    const person = {lat: Number(item.lat), lng: Number(item.lng)};
                    //console.log(person);

                    const marker = new google.maps.Marker({
                        position: person,
                        map: map,
                    });

                    // declare a variable called batteryLevel
                    let batteryLevel;
                    if (typeof item.battery === "string" && item.battery.includes(":")) {
                        batteryLevel = item.battery.split(":")[1];
                    } else {
                        batteryLevel = "0%";
                    }
                    var status = this.getStatus(item.lastSeen);




                    const infowindow = new google.maps.InfoWindow({
                        content: `<p>Zone: ${item.zone}" <a href="tel:${item.phoneNumber}"/>${item.phoneNumber}</a></p></p><h6 class="text-capitalize d-flex align-items-center"><span class="offline-agent mr-2"></span> <span> <a @click.native="goToLogs(item.idNo)" href="javascript:void(0)">${item.names}</a><small><strong>(${item.status}) ${this.humanDateTimeAgo(item.lastSeen)}</strong></small></span></h6> <p class="pb-0 mb-0">Last seen at <strong>${item.address}</strong> at ${this.formattedTime(item.lastSeen)} </strong></p><p>Most recent activity: <strong>${item.des}</strong><div class="listview text-align-left text-capitalize pl-0">  <div class="listview__header text-align-left text-capitalize text-left"><strong>Target Achievement <strong>(0%) KES 0</strong> of KES 0 per day</strong></div> <div class="progress"> <div class="progress-bar progress-bar-warning" style="width: 75%" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div> </div></div></p>`,
                    });
                    let url;


                    if (status === "Active") {
                        url = "assets/images/users/active-agent.png"
                        this.activeAgent++;
                    } else if (status === "Inactive") {
                        url = "assets/images/users/inactive-agent.png"
                        this.inactiveAgent++;
                    } else {
                        url = "assets/images/users/dormant-agent.png"
                        this.dormantAgent++;
                    }

                    //inactive agent icon
                    var agent = {
                        url: url, // url

                        origin: new google.maps.Point(0, 0), // origin
                        anchor: new google.maps.Point(0, 0), // anchor
                        scaledSize: new google.maps.Size(19, 40) // size in pixels
                    };

                    marker.setIcon(agent);
                    marker.addListener("mouseover", () => {
                        infowindow.open(map, marker);
                        this.refresh = false

                    });
                    marker.addListener("mouseout", () => {
                        infowindow.close();
                        this.refresh = true
                    });
                });


            },


        }
